::-webkit-scrollbar-thumb {
  border-radius: 100px;
  border: 6px solid rgba(0, 0, 0, 0.18);
  border-left: 0;
  border-right: 0;
  background-color: #50e281;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Yaldevi', sans-serif;
}
a{
 text-decoration: none;
}
.main-container{
  display: flex;
}
main{
 width:100%;
 overflow: scroll;
 overflow-x: hidden;
 height: 100vh;
}
.title{
  font-size: 30px;
}


.sidebar{
  background: #05551f;
  color: #ffffff;
  height: 100vh;
  overflow: scroll;
  overflow-x: hidden;
  border-top-right-radius: 5px;
}

/* .link_text{
  color: aliceblue;
} */
.routes{
  color: white;
}
.top-section{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 10px;
}
.logo{
  /* padding: 1rem; */
  font-size: 18px;
  line-height: 0;
}
.search{
  display: flex;
  align-items: center;
  margin: 10px 0;
  height: 30px;
  padding: 10px;
}
.search input {
  border: none;
  margin: 0px 10px;
  border-radius: 5px;
  background: #ffffff;
}

.link {
  display: flex;
  color: white;
  gap: 0px;
  padding: 15px 15px;
  border-right: 4px solid transparent;
  transition: 0.2s cubic-bezier(0.6, -0.28, 0.735, 0.045);
}

.link:hover{
  border-right: 8px solid white;
  transition: 0.2s cubic-bezier(0.6, -0.28, 0.735, 0.045);
  background: #dee2fc;
  color:#000;
}

.active{
  border-right: 4px solid white;
  background: #dee2fc;
  color: #000;
}
.icon{
  display: flex;
  justify-content: space-between;
  color: white;
  
  gap: 10px;
}
.link_text{
  white-space: nowrap;
  font-size: 15px;
  padding-left: 10px;
  font-weight: 600;
}

.menu {
  display: flex;
  justify-content: space-between;
  color: #ffffff;
  gap: 10px;
  cursor:pointer;
  padding: 15px 15px;
  border-right: 4px solid transparent;
  transition: 0.2s cubic-bezier(0.6, -0.28, 0.735, 0.045);
}

.menu_item{
  display: flex;

}
.menu_container .link{
  padding-left: 20px;
  border-bottom: #fff 0.5px solid;
letter-spacing: .9px;;
}


#ele_ment_lead{
  background-color:#DEE2FC;
  padding:20px 20px;
  width:100%;
}
